@import url(https://fonts.googleapis.com/css?family=Montserrat:300,500,800&display=swap);
* {
    margin: 0;
    padding: 0;
    border: 0;
    font-family: 'Montserrat', sans-serif;
}

body {
    background-color: #F2F2F2;
    max-width: 100%;
    width: 100%;
    width: 100vw;
}
a{
    outline: none;
    cursor: none;
    text-decoration: none;
}
.container{
    width: 100%;
    width: 100vw;
    display: grid;
    grid-template-areas:
        'logo'
        'main'
        'image';
}

.logo {
    grid-area: logo;
    margin: auto;
}

.main { 
    grid-area: main;
    text-align: center;
}

.title {
    padding: 3vw;
    font-size: 3rem;
    font-weight: 800;
}

.image {
    grid-area: image;
}


img.main-image-mobile {
    max-width: 100vw;
}

img.main-image {
    visibility: hidden;
}

.logo-image{
    width: 50vw;
    padding: 5vw;
}

button {
    display: flex;
    justify-content: space-around;
    margin: auto;
    padding: 0.8rem;
    border-radius: 3rem;
    background-color: #15573F;
    color: #FFF;
    width: 10rem;
    font-weight: 500;
    font-size: 0.9rem;
}

@media (min-width:801px) {
    body {
        overflow: hidden;
    }

    .container {
        width: 80%;
        height: 700px;
        margin: auto;
        display: grid;
        grid-template-areas:
            'logo image'
            'main image';
        grid-template-columns: 1fr 2fr;
        grid-template-rows: auto 3fr;
    }

    img.main-image-mobile {
        visibility: hidden;
        height: 0;
        width:0;
    }

    img.main-image {
        visibility: visible;
        max-width: 80vw;
        margin-top: 20px;
        margin-left: -100px;
    }

    .logo{
        padding: 3vw;
        margin: 0;
        min-width: 330px;
    } 
    
    .main{
        padding-left: 3vw;
        text-align: left;
    }

    .logo-image{
        width: 10rem;
        padding: 0;
    }

    .title {
        padding: 3rem 0 2rem;
    }

    button{
        margin: 0;
    }

    #btn-start:hover{
        cursor: pointer;
        outline: none;
        box-shadow: 1px 1px 9px -1px rgba(21,87,63,1);
    }
}

@media (max-width:800px) {
    body {
        overflow-x: hidden;
    }
    img.main-image {
        height: 0;
        width:0;
    }
}
.questionBlock {
    border: 1px solid #555;
    padding: 40px;
    background-color: #aaa;
}

.optionButton {
    margin: 10px;
    padding: 10px;
    border: 1px solid #555;
    background-color: #fff;
    display: inline;
}

.question {
    margin: 10px;
    padding: 10px;
}

.imgQuestion {
    border: 1px solid #000;
    width: 50px;
    height: 50px;
}

.selected {
    background-color: red;
}
svg.iconPlantList{
    width: 24px;
    height: 24px;
}
